	module.exports = {"data":{"site":{"siteMetadata":{"title":"Nobody Listens Anyway","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2008-02-27-cache-county-library-system/index.md absPath of file >>> MarkdownRemark","html":"<p>Right now in Cache County each city has its own library system and citizens of one city are not allowed to checkout books from a library in another city unless they pay for a very expensive library card. This is unfortunate as it creates a barrier to information for those who really need it. By combining resources the libraries will be able to provide a greater number of resources to everyone in the county. There is an initiative to form a county wide library system. <a href=\"http://www.petitiononline.com/CACHELIB/petition.html\">Sign the petition and provide access to all the materials for everyone</a>.</p>","frontmatter":{"title":"Cache County Library System","date":"February 27, 2008","image":null}}},"pathContext":{"path":"/cache-county-library-system"}}

